import React, { useState, useEffect, useReducer } from "react";
import { navigate } from "@reach/router";
import { Tabs, Input, Button, Radio, Select } from "antd";
const { TabPane } = Tabs;
const { Option } = Select;

import { SettingIcon } from "../../icons";
import { Icon20HomeOutline } from "@vkontakte/icons";

import { dataTemplatePages } from "../../helpers/dataTemplatePages";

import "./AdminPanelMain.scss";

const AdminPanelMain = ({ getPlatform, getButtonStats, editLinkGroup }) => {
  const [actionPage, setActionPage] = useState("");
  const [actionLink, setActionLink] = useState("msg");
  const [linkGroup, setLinkGroup] = useState("");

  function onChangeActionPage(value) {
    // const value = e.target.value;
    console.log("onChangeActionPage", value);
    setActionPage(value);
  }

  function onChangeAddLink(e) {
    const value = e.target.value.trim();
    console.log("onChangeAddLink", value);
    setLinkGroup(value);
  }

  function onChangeTypeActionLink(e) {
    const value = e.target.value.trim();
    console.log("onChangeAddLink", value);
    setActionLink(value);
  }

  console.log("getButtonStats", getButtonStats);

  const arrStats = [];

  for (let key in getButtonStats) {
    const nameItem = key.replace("SquidGameAPP_", "");
    if (key.includes("SquidGameAPP") && !nameItem.includes("SquidGameAPP")) {
      arrStats.push({ [nameItem]: getButtonStats[key] });
    }
  }

  const options = dataTemplatePages.map((item) => {
    return (
      <Option key={item.name} value={item.name}>
        {item.name}
      </Option>
    );
  });

  return (
    <Tabs
      defaultActiveKey="pages"
      centered
      tabPosition="top"
      style={{ marginTop: getPlatform !== "web" ? "50px" : "0" }}
    >
      <TabPane
        tab={
          <div
            className="admin-panel-container__icon"
            onClick={() => navigate("squid-game")}
            // onClick={() => navigate("/")}
          >
            {" "}
            <Icon20HomeOutline />
          </div>
        }
        key="home"
      ></TabPane>
      <TabPane tab="Страницы" key="pages">
        <div className="admin-panel-container" key="pages_1">
          <div className="radio">
            <div className="title"> Выберите страницу для изменения</div>
            <Select
              defaultValue="main"
              style={{ width: 120 }}
              onChange={onChangeActionPage}
            >
              {options}
            </Select>
          </div>

          <div className="radio">
            <Radio.Group onChange={onChangeTypeActionLink} value={actionLink}>
              <Radio value="msg">Подписка на рассылку</Radio>
              <Radio value="sub">Подписка на группу</Radio>
            </Radio.Group>
          </div>

          <Input
            placeholder="Подписка на рассылку"
            onChange={onChangeAddLink}
            value={linkGroup}
            type="text"
          />
          <Button
            type="primary"
            onClick={() => editLinkGroup(linkGroup, actionLink, actionPage)}
          >
            Изменить
          </Button>
        </div>
      </TabPane>

      <TabPane tab="Статистика" key="statistics">
        <div className="admin-panel-container__statistics">
          {arrStats?.map((item, index) => {
            return (
              <div className="item" key={index}>
                <div className="title"> {Object.keys(item)} </div>
                <div className="count">{Object.values(item)}</div>
              </div>
            );
          })}
        </div>
      </TabPane>
    </Tabs>
  );
};

export { AdminPanelMain };
