import React, { useState, useEffect, useReducer } from "react";
import { Button } from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import cn from "classnames";
import "./ResultPanel.scss";

import { APP_ID_TARGET, APP_IMG_SHARING_STORIES } from "../../constants";
import { nativeAds } from "../../ads";
import { addGroup, incrementCountButton } from "../../bridge-method";
import { story } from "../../sharing-method";
import { navigate } from "@reach/router";

const ResultPanel = ({
  id,
  go,
  IMGresult,
  getPlatform,
  openAlert,
  snackbar,
  fetchedUser,
  getGroupId,
  appID,
  imgIndex,
}) => {
  useEffect(() => {
    setTimeout(() => {
      nativeAds();
    }, 3000);

    incrementCountButton(`stats.SquidGameAPP_Result`);
  }, []);

  const openNewApp = () => {
    bridge
      .send("VKWebAppOpenApp", { app_id: APP_ID_TARGET, location: "new-app" })
      .then((res) => {
        incrementCountButton(`stats.SquidGameAPP_Result`);
      })
      .catch((err) => {});
  };

  return (
    <div
      className={cn({
        "result-panel": true,
        web: getPlatform === "web",
      })}
    >
      <div className="img">{IMGresult}</div>

      <Button
        className="buttons"
        onClick={() => story(APP_IMG_SHARING_STORIES[imgIndex], "PageResult")}
      >
        Поделиться с друзьями
      </Button>
      <Button className="buttons" onClick={() => navigate("squid-game")}>
        Пройти еще раз
      </Button>
    </div>
  );
};

export { ResultPanel };
