import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import { navigate } from "@reach/router";
import { Tabs, Input, Button, Radio, Select } from "antd";
// import { Button } from "@vkontakte/vkui";
import cn from "classnames";

import "./TemplatePage.scss";

import { story } from "../../sharing-method";
import { nativeAds } from "../../ads";
import { addGroup, incrementCountButton } from "../../bridge-method";
import {
  GROUP_ID_MAILING_PAGE_1,
  APP_IMG_SHARING_STORIES,
  GROUP_ID_SUBSCRIPTION_PAGE_1,
  APP_ID_DEFAULT,
} from "../../constants";

import { imgs } from "../../helpers/images";

const TemplatePage = ({
  icon,
  header,
  title,
  description,
  buttonName,
  setTemplatePage,
  name,
  next,
  fn,
  appID,
  getGroupId,
  openAlert,
  fetchedUser,
  getRandomImg,
  imgIndex,
  gotToken,
  publishStories,
  nameRadio,
  correctAnswer,
  img,
  index,
}) => {
  const [editTitle, setEditTitle] = useState(title);
  const [radioValue, setRadioValue] = useState("");

  function subscribeMessageFromGroupDefault(groupIDsubscription, type, next) {
    bridge
      .send("VKWebAppAllowMessagesFromGroup", {
        group_id: groupIDsubscription,
      })
      .then((res) => {
        if (type === "search") {
          setTemplatePage(next);
        }
        incrementCountButton(`stats.SquidGameAPP_${type}`);
      })
      .catch((err) => {
        bridge
          .send("VKWebAppAllowMessagesFromGroup", {
            group_id: groupIDsubscription,
          })
          .then((res) => {
            if (type === "search") {
              setTemplatePage(next);
            }
            incrementCountButton(`stats.SquidGameAPP_${type}`);
          })
          .catch((err) => {
            bridge
              .send("VKWebAppAllowMessagesFromGroup", {
                group_id: groupIDsubscription,
              })
              .then((res) => {
                if (type === "search") {
                  setTemplatePage(next);
                }
                incrementCountButton(`stats.SquidGameAPP_${type}`);
              })
              .catch((err) => {
                if (type === "search") {
                  setTemplatePage(next);
                }
              });
          });
      });
  }

  useEffect(() => {
    const isInteger = Number.isInteger(index / 3);

    if (name !== "main" && isInteger) {
      nativeAds();
    }

    switch (name) {
      // case "Pag1":
      //   break;
      case "Page31":
        getRandomImg(0, 8);
        break;
      default:
        break;
    }
  }, []);

  const radioHandleChange = (e) => {
    console.log("radioHandleChange", e.target.value);
    console.log("correctAnswer", correctAnswer);

    setRadioValue(e.target.value);
    if (correctAnswer !== e.target.value) {
      setTemplatePage("main");
    } else if (correctAnswer === e.target.value && name === "Page31") {
      story(APP_IMG_SHARING_STORIES[imgIndex], name);
    } else {
      setTemplatePage(next);
    }
  };

  return (
    <div className="bg">
      <div className="container">
        {name !== "main" && (
          <div className="img">
            <img src={imgs[img]?.default} alt="bg" />
          </div>
        )}

        <div className="container__main">
          <div className="header">{header}</div>
          <div className="title">{editTitle}</div>
          <div className="description">{description}</div>
        </div>

        {buttonName && (
          <div className="buttons">
            <Button
              onClick={() => {
                if (name === "Page31") {
                  story(APP_IMG_SHARING_STORIES[imgIndex], name);
                  // gotToken && publishStories();
                } else {
                  setTemplatePage(next);
                }
              }}
              className="buttons"
            >
              <span>{buttonName}</span>
            </Button>
          </div>
        )}

        {nameRadio && nameRadio.length > 0 && (
          <div className="radio">
            <Radio.Group onChange={radioHandleChange} value={radioValue}>
              {nameRadio.map((item) => {
                return (
                  <Radio key={item} value={item}>
                    {item}
                  </Radio>
                );
              })}
            </Radio.Group>
          </div>
        )}
      </div>
    </div>
  );
};

export { TemplatePage };
